<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('Add Resource Type') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                        <span class="user-guide">
                            <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG" role="button"
                                aria-expanded="false" aria-controls="collapseExample"> <em
                                    class="fa fa-files-o pr-2"></em>{{ $t('GuideForUser') }}</a>
                            <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                    <div class="con" v-html="$t('UgAddEditResourceType')"></div>
                                </div>
                            </div>
                        </span>
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        ResourceTypeId: {
            type: Number,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('ResourceTypeName'),
                            name: "ResourceTypeName",
                            value: "",
                            placeholder: this.$t('EnterResourceTypeName'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            config: {
                                options: [{ value: "1001", name: "Active" }, { value: "1002", name: "InActive" }]
                            },
                            placeholder: this.$t('SelectStatus'),
                            validationRules: { "required": true, max: 100 },
                        },
                    ],
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "CustomSelectField",
                            mode: "tag",
                            label: this.$t('User '),
                            name: "User",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('SelectUser'),
                            validationRules: { "required": true, max: 100 },
                        },
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('description'),
                            name: "Description",
                            value: "",
                            placeholder: this.$t('EnterDescription'),
                            validationRules: "",
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ],
            ListData: []
        }
    },
    created: async function () {
        this.FetchData();
        this.GetDdlRecords_User();
        // this.GetDdlRecords_STATUS();
    },
    methods: {
        FetchData: function () {
            var vueObj = this;
            vueObj.isLoading=true;
            var url = `Id=${vueObj.ResourceTypeId}`;
            DataServices.FetchDataByResourcetypeId(url).then(
                function (response) {
                    vueObj.isLoading=false;
                    if (response.data != null && response.data != "") {
                        if (response.data.status == "Success") {
                            (vueObj.FormSchema)[0].Data[0].value = response.data.result.data[0].resource_type_name;
                            (vueObj.FormSchema)[0].Data[1].value = response.data.result.data[0].status_id;
                            (vueObj.FormSchema)[2].Data[0].value = response.data.result.data[0].description;
                            JSON.parse(response.data.result.data[0].FSMUserListId).forEach((item, index) => {
                                vueObj.ListData.push({
                                    value: item.value,
                                    label: item.name
                                })
                            })
                            vueObj.FormSchema[1].Data[0].value = vueObj.ListData;
                        }
                    }
                }
            );
        },
        GetDdlRecords_User: async function () {
            var vm = this;
            var appendingUrl = `id=&&moduleName=${'Ticketing'}&type=${'FSM_SA_User'}&search=&refId=`;
            await DataServices.GetDDLData(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        response.data.DATA.forEach((item, index) => {
                            vm.FormSchema[1].Data[0].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            });
        },
        // GetDdlRecords_STATUS: async function () {
        //     var vm = this;
        //     var appendingUrl = `id=&&moduleName=${'FSM'}&type=${'GLOBAL_STATUS'}&search=&refId=`;
        //     await DataServices.GetDDLData(appendingUrl).then(function (response) {
        //         if (response.data) { 
        //             debugger
        //             if (response.data != null) {
        //                 vm.FormSchema[0].Data[1].config.options.push(response.data.DATA);
        //             }
        //         }
        //     });
        // },
        Close: function () {
            this.$parent.AddResourceTypePopup = false;
        },
        onSubmit(formfields, val) {
            var vm = this;
            vm.isLoading=true;  
            var FSMUserList = '';
            var i = 0;
            for (i = 0; i < formfields.User.length; i++) {
                if (i == 0) {
                    FSMUserList = formfields.User[i].value;
                } else {
                    FSMUserList = FSMUserList + "," + formfields.User[i].value;
                }
            }
            var jsonobj = {
                Id: vm.ResourceTypeId == null ? 0 : vm.ResourceTypeId,
                ResourceTypeName: formfields.ResourceTypeName,
                FSMUsersList: FSMUserList,
                Desc: formfields.Description,
                Status: formfields.Status
            };
            var params = JSON.stringify(jsonobj);
            DataServices.SaveResourceTypeWithResources(params).then((response) => {
                vm.isLoading=false; 
                if (response.data.status == 'Success') {
                    if (vm.ResourceTypeId > 0) {
                        vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("ResourceType")]), "success", true, vm.$t("Alert"));
                        vm.Close();
                    }
                    else {
                        vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("ResourceType")]), "success", true, vm.$t("Alert"));
                    }
                    vm.Close();
                    vm.$parent.FetchRecords();
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Resource")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
}
</script>