<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list v-show="IsResourceTypeView" :IsShowAction="true" ModuleName="FSM" SubModuleCode="FSM_resource_type"
            :LegendArray="[]" :widgets="[]" IdentityColumn="resource_type_id" :HeaderText="$t('ResourceType')"
            :listType="['List']" :ListData="ResourceTypeList" :HeaderData="Headers" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchRecords"
            :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'status_id'">
                    <listing-status :isfor="'RESOURCES_TYPE'" v-if="isCRMShowStausDrop(data.row.status_id, 'Ticketing')"
                        ref="updatestatus" v-bind:dataid="data.row.resource_type_id" data-controller="Crm"
                        v-bind:statuslist="StatusListForListing" v-bind:item="data.status_id" data-action="index"
                        v-bind:dataselected="data.row.status_id == 1001 ? 'Active' : 'Inactive'"
                        v-bind:callbackfunction="FetchRecords" v-bind:isselectedvaluestring="true">
                    </listing-status>
                    <a class="inactive-disabled" v-if="!isCRMShowStausDrop(data.row.status_id, 'Ticketing')">
                        <span v-bind:class="getStatusClass(data.row.status_id, 'status_id')">
                            {{ data.row[data.column.COLUMN_NAME] }}
                        </span>
                    </a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'CREATED_AT'">
                    <span>
                        {{ $options.filters.formatDate(data.row.CREATED_AT) }}
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'resource_type_name'">
                    <a href="#" @click="openPopupForSetupTickectSource(data.row.resource_type_id)">{{ data.row.resource_type_name }}</a>
                </template>
            </template>
        </tg-list>
        <AddResourceTypePopup v-if="AddResourceTypePopup" :ResourceTypeId="ticketSourceId"></AddResourceTypePopup>
    </section>
</template>

<script>
import Schema from "@/views/ResourceType/ResourceTypeSchema";
import DataServices from "@/services/DataService";
import AddResourceTypePopup from '../../components/Modal/AddResourceTypePopup.vue'
export default {
    components: { AddResourceTypePopup },
    data() {
        return {
            isLoading:false,
            IsResourceTypeView: true,
            AddResourceTypePopup: false,
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            Headers: [{
                "COLUMN_NAME": "resource_type_name",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Resource Type Name",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "CREATED_AT",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Created at ",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "CreatedBy_Name",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Created by",
                "DISPLAY_ORDER ": 3
            },
            {
                "COLUMN_NAME": "status_id",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "status",
                "DISPLAY_ORDER": 4,
            },
            ],
            showSetupTickectSource: false,
            showExpanded: '',
            StatusMappingList: [],
            NoRecord: false,
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            searchCondition: '',
            ResourceTypeList: [],
            GroupStart: 1,
            GroupEnd: 3,
            ResourceTypeName: '',
            ResourceTypeSearch: false,
            ticketSourceId: 0,
            CreationDate: '',
            IsDateSearch: false,
            conditionForResourceTypeSearch: "",
            search: "",
            StatusListForListing: [{ STATUS: 'Active', STATUS_ID: 1001, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#28a745" },
            { STATUS: 'Inactive', STATUS_ID: 1002, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#d72435" }],
        }
    },
    created: async function () {

        this.user_id = this.GetUserInfo.ID
        this.user_type = this.GetUserInfo.UserType
        this.companyId = this.GetUserInfo.CompanyId;
        await this.FetchRecords();
    },

    methods: {
        actionButtonClick(callbackfunction, item, index, event) {
            switch (callbackfunction) {
                case 'Edit':
                    this.openPopupForSetupTickectSource(item.resource_type_id)
                    break;
                case 'Delete':
                    this.DeleteResourceType(item.resource_type_id);
                    break;
                case 'ClickOnView':
                    this.ResourceTypeDetail(item.resource_type_id, item.resource_type_name);
                    break;
            }
        },
        ResourceTypeDetail: function (id, name) {
            var vm = this;
            vm.$router.push({
                name: 'ResourceTypeDetail', params: {ResourceTypeId: id , ResourceTypeName: name}
            });
        },
        getStatusClass: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 bg-success text-center text-white d-block';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    case 'ClickOnView':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchRecords();
        },
        paramsToObject(entries) {
            const result = {}
            for (const [key, value] of entries) { // each 'entry' is a [key, value] tupple
                result[key] = value;
            }
            return result;
        },
        leftCommonSearch: function (value) {            
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;            
            if (typeof value.searchCondition != 'undefined') {
              this.searchCondition = value.searchConditionJson;             
            }
            else {
              this.searchCondition = "";
            }
            this.FetchRecords();
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "commonDeleteData":
                    this.DeleteResourceType();
                    break;
                case "openaddWorkType":
                    this.openPopupForSetupTickectSource(null);
                    break;

            }
        },
        isBlank: function (str) {
            return (!str || /^\s*$/.test(str));
        },
        openPopupForSetupTickectSource: function (id) {
            if (id == undefined)
                id = 0;
            this.ticketSourceId = id;
            this.AddResourceTypePopup = !this.AddResourceTypePopup;
        },
        openPopUpForResourceTypeDetail: function (id) {
            if (id == undefined)
                id = 0;
            this.ticketSourceId = id;
            this.AddResourceTypePopup = !this.AddResourceTypePopup;
        },
        formatDateTime: function (value) {
            if (value) {
                return moment(value).format(String(Globalize.culture().calendar.patterns.d).toUpperCase() + " " + String(Globalize.culture().calendar.patterns.t).replace("t", "A").replace("t", ""))
            }
        },
        sortdata: function (sortBy) {

            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchRecords();
        },

        Searchdata: function () {
            var vm = this;
            //do something
            vm.searchCondition = vm.searchCondition;
            vm.FetchRecords();
        },
        refreshList: function () {
            this.searchCondition = "";
            this.FetchRecords();
        },
        DeleteResourceType: function (event) {
            var vm = this;
            var val = "";
            if ($('.chkItems:checkbox:checked').prop('checked') == true) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            } else {
                val += event;
            }
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Resource Type")), function () {
                    var postJSON = {
                        ids: val,
                        companyId: vm.companyId,
                        userId: vm.user_id,
                        moduleName: "FSM",
                        refCode: "TALYGEN_FSM_RESOURCE_TYPE"

                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.deleteActivityData(objectPostString).then(response => {
                        var box = "";
                        if (response.data[0].Status == "Success") {                           
                            vm.FetchRecords();
                            box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Resource Type')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Resource Type')]), "failure", true, vm.$t('Alert'));
                        }
                        setTimeout(function () {
                            box.modal('hide');
                        }, 2000);

                    });
                });
            }
            else {
                alert(vm.$t("Selectanyrecordtodelete"));
            }
        },
        FetchRecords: async function () {
            
            var vueObj = this;
            vueObj.isLoading=true;
            var url = `sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}&${vueObj.searchCondition}`;
            vueObj.ResourceTypeList = [];
            await DataServices.GetFsmResourceTypeList(url)
                .then(function (response) {
                    vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                    vueObj.TotalRecords = 0;
                    vueObj.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "status_id") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                        if (column.COLUMN_NAME == "resource_type_name") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                        if (column.COLUMN_NAME == 'View') {
                            column.settings = {
                                isInSlot: true
                            }
                        }
                        if (column.COLUMN_NAME == 'Action') {
                            column.settings = {
                                isInSlot: true
                            }
                        }
                        if (column.COLUMN_NAME == 'CREATED_AT') {
                            column.settings = {
                                isInSlot: true
                            }
                        }
                    });
                    if (response.data != null && response.data != "") {
                        if (response.data.status == "Success") {
                            vueObj.ResourceTypeList = response.data.result.data;
                            vueObj.listheaderbuttons[1].isdisabled=true;
                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;

                            if (vueObj.ResourceTypeList.length > 0) {
                                vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                                vueObj.NoRecord = false;
                                vueObj.isLoading=false;
                                vueObj.listheaderbuttons[1].isdisabled=true;
                            }
                            else {
                                vueObj.NoRecord = true;
                                vueObj.isLoading=false;
                            }
                        }
                        else {
                            vueObj.NoRecord = true;
                            vueObj.isLoading=false;
                        }                       
                        setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
                    }
                });
        }
    }
}
</script>

